'use client';
import { Disclosure } from '@headlessui/react';
import { IconLogin2, IconMenu2, IconShoppingBag, IconX } from '@tabler/icons-react';
import { Link } from 'gatsby';

const navigation = [
  { name: 'About', href: '/about' },
  { name: 'Rush', href: '/rush' },
];

const externalLinks = [
  { name: 'Store', href: 'https://chigammastore.square.site/', icon: IconShoppingBag },
  { name: 'Member Portal', href: 'https://cmt.thetatau.org', icon: IconLogin2 },
]

function classNames(...classes: string[]) {
  return classes.filter(Boolean).join(' ');
}

export default function Navbar() {
  const { pathname } = useLocation();
  return (
    <Disclosure as="nav" className="bg-gray-50">
      {({ open }) => (
        <>
          <div className="mx-auto px-2 sm:px-6 lg:px-20">
            <div className="relative flex h-28 items-center justify-between">
              <div className="flex flex-1 items-center justify-start sm:items-stretch sm:justify-between">
                <div className="flex flex-shrink-0 items-center">
                  <Link to="/" className='mr-4'>
                    <img
                      className="h-16 w-auto"
                      src="/ttxg.svg"
                      alt="Theta Tau Chi Gamma logo"
                    />
                  </Link>
                  <div className="hidden sm:block sm:ml-6">
                    {navigation.map((item) => (
                      <Link
                        key={item.name}
                        to={item.href}
                        className={classNames(
                          pathname === item.href
                            ? 'text-dark-red '
                            : 'hover:text-dark-red ',
                          'rounded-md px-3 py-2 text-lg font-medium transition-all duration-100'
                        )}
                        aria-current={
                          pathname === item.href ? 'page' : undefined
                        }
                      >
                        {item.name}
                      </Link>
                    ))}
                  </div>
                </div>
                <div className="hidden sm:ml-6 sm:block">
                  <div className="flex space-x-4 h-full items-center">
                    {externalLinks.map((item) => (
                      <Link
                        key={item.name}
                        to={item.href}
                        className={classNames(
                          pathname === item.href
                            ? 'text-dark-red '
                            : 'hover:text-dark-red ',
                          'rounded-md px-3 py-2 text-lg font-medium transition-all duration-100 inline-flex items-center'
                        )}
                        aria-current={
                          pathname === item.href ? 'page' : undefined
                        }
                      >
                        <item.icon size={24} stroke={1.5} className="mr-2" />
                        {item.name}
                      </Link>
                    ))}
                  </div>
                </div>
              </div>
              <div className="absolute inset-y-0 right-0 flex items-center sm:hidden">
                {/* Mobile menu button*/}
                <Disclosure.Button className="relative inline-flex items-center justify-center rounded-md p-2 text-gray-400 hover:bg-dark-red hover:text-white focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white">
                  <span className="absolute -inset-0.5" />
                  <span className="sr-only">Open main menu</span>
                  {open ? (
                    <IconX className="block h-6 w-6" aria-hidden="true" />
                  ) : (
                    <IconMenu2 className="block h-6 w-6" aria-hidden="true" />
                  )}
                </Disclosure.Button>
              </div>
            </div>
          </div>

          <Disclosure.Panel className="sm:hidden">
            <div className="space-y-1 px-2 pb-3 pt-2">
              {navigation.map((item) => (
                <Disclosure.Button
                  key={item.name}
                  as="a"
                  href={item.href}
                  className={classNames(
                    item.href === pathname
                      ? 'bg-dark-red text-white'
                      : 'text-gray-600 hover:bg-darker-red hover:text-white',
                    'block rounded-md px-3 py-2 text-base font-medium'
                  )}
                  aria-current={item.href === pathname ? 'page' : undefined}
                >
                  {item.name}
                </Disclosure.Button>
              ))}
              {externalLinks.map((item) => (
                <Disclosure.Button
                  key={item.name}
                  as="a"
                  href={item.href}
                  className={classNames(
                    item.href === pathname
                      ? 'bg-dark-red text-white'
                      : 'text-gray-600 hover:bg-darker-red hover:text-white',
                    'block rounded-md px-3 py-2 text-base font-medium'
                  )}
                  aria-current={item.href === pathname ? 'page' : undefined}
                >
                  {item.name}
                </Disclosure.Button>
              ))}
            </div>
          </Disclosure.Panel>
        </>
      )}
    </Disclosure>
  );
}
